@font-face { 
    font-family: 'yg-jalnan'; 
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_four@1.2/JalnanOTF00.woff') format('woff'); 
    font-weight: normal; 
    // font-style: normal; 
}

html, body, head, h1, h2, h3, h4, h5, h6, p, a, li, ol, ul, div, table, thead, tbody, th, td, tr, img {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a:active {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

#app-container {
    max-width: 1024px;
    margin: 0 auto;
}

.common-title {
    font-family: 'yg-jalnan';
    font-size: 15px;
    margin-bottom: 10px;
}

.block-wrapper {
    padding: 30px;
    box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
    margin-top: 30px;
    margin-bottom: 30px;
    background-color: white;
    border-radius: 8px;
}

.common-contents {
    font-size: 13px;
    line-height: 17px;
    color: #616161;
}

@media (max-width: 430px) {
    #app-container {
        padding-left: 15px;
        padding-right: 15px;
    }
}